import React, { useState, useEffect } from "react";
import Header from "./conponent/parts/Header";
import "./App.css";
import Footer from "./conponent/parts/Footer";
import Grid from "@material-ui/core/Grid";
import { Link } from "react-router-dom";

interface Saple {
  month: String;
  name: String;
  color: String;
}

const App: React.FC = (props: any) => {
  const [posts, setPosts] = useState<Saple[]>([]);

  //第一引数:関数、第二引数:コールバックを設定 ( [name] を設定したらnameの状態が変化する旅呼ばれる)
  //レンダリングの後、レンダリングされるたびに呼ばれる
  //第二引数にからの配列を入れることで最初のレンダリング時だけに呼ばれる
  useEffect(() => {
    //fetch(input: RequestInfo, init?: RequestInit)のreturn typeはPromise<Response> になる
    fetch("http://localhost:8090/api/getBirthStone", { method: "GET" })
      .then((res) => res.json())
      .then((data) => {
        setPosts(data);
      });
  }, []);

  return (
    <div className="App">
      <Header title="Menu" />
      <h1>Menu</h1>

      <Grid container spacing={3}>
        <Grid item xs={3}>
          <Link to={"/TaskApp"}>
            <h1>Task App</h1>
          </Link>
        </Grid>
        <Grid item xs={3}>
          <h1>test</h1>
        </Grid>
        <Grid item xs={3}>
          <h1>test</h1>
        </Grid>
        <Grid item xs={3}>
          <h1>test</h1>
        </Grid>
      </Grid>

      <p>app</p>
      <div>
        <ul>
          {posts.map((post) => (
            <p>
              name:{post.name} month:{post.month} color:{post.color}
            </p>
          ))}
        </ul>
      </div>
      <Footer />
    </div>
  );
};

export default App;
