import React, { useState, useEffect } from "react";
import Header from "../parts/Header";
import Footer from "../parts/Footer";
import { Link } from "react-router-dom";
import FormControl from "@material-ui/core/FormControl";
import { TextField, List } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import AddToPhotosIcon from "@material-ui/icons/AddToPhotos";
import styles from "../../css/App.module.css";
import TaskItems from "../parts/TaskItems";

const useStyles = makeStyles((theme) => ({
  filed: {
    marginTop: 30,
    marginButtom: 20,
  },
}));

interface task {
  taskId: Number;
  taskTitle: String;
  creatDate: String;
  userId: String;
  complete: Boolean;
  like: Boolean;
}

const TaskApp: React.FC<task> = (props: any) => {
  const classes = useStyles();

  const [input, setInput] = useState("");
  const [posts, setPosts] = useState<task[]>([]);

  const newTask = () => {};

  useEffect(() => {
    fetch("http://localhost:8090/api/taskItems", { method: "GET" })
      .then((res) => res.json())
      .then((data) => {
        setPosts(data);
        console.log("aaaaaaa");
        console.log(posts);
      });
  }, []);

  return (
    <div className="App">
      <Header title="TaskApp" />
      <Link to="/">メニュー</Link>
      <p>TaskApp</p>

      <FormControl>
        <TextField
          className={classes.filed}
          label="New task ?"
          value={input}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setInput(e.target.value)
          }
        />
      </FormControl>
      <button disabled={!input} onClick={newTask} className={styles.app__icon}>
        <AddToPhotosIcon />
      </button>

      <br />
      <List>
        {posts.map((task) => (
          <TaskItems
            taskId={task.taskId}
            taskTitle={task.taskTitle}
            creatDate={task.creatDate}
            userId={task.userId}
            complete={task.complete}
            like={task.like}
          />
        ))}
      </List>

      <Footer />
    </div>
  );
};

export default TaskApp;
