import React from "react";
//import { makeStyles } from "@material-ui/core/styles";

//const useStyles = makeStyles((theme) => ({}));

interface task {
  taskId: Number;
  taskTitle: String;
  creatDate: String;
  userId: String;
  complete: Boolean;
  like: Boolean;
}

const TaskItems: React.FC<task> = (props: task) => {
  //const classes = useStyles();
  return (
    <div>
      <p>{props.taskId}</p>
      <p>{props.taskTitle}</p>
      <p>{props.creatDate}</p>
      <p>{props.userId}</p>
    </div>
  );
};

export default TaskItems;
